.intropart1{
    width: 80%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.left{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.left h1{
    font-size: 1rem;
    color: #5854E2;
}
.left h2{
    font-size: 3.5rem;
    color: #1E266D;
}
.left h2 span{
    font-size: 3.5rem;
    color: #F39D00;
}
.left h3{
    font-size: 1.2rem;
    color: #3C3565;
}
.button-group{
    margin-top: 50px;
}
.button-group a{
    padding: 15px 25px;
    border-radius: 5px;
    font-size: 0.9rem;
}
.button-group a i{
    margin: 0 10px;
    border-radius: 50%;
    background-color: #f0ac0e;
    background-image: linear-gradient(90deg,#f0ac0e 0%,#f56616 100%);
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: #fff;
}
.button-group a i:hover{
    background-image: linear-gradient(90deg,#f0ac0e 0%,#f56616 40%);
}
.button-group a:nth-child(1){
    background-color: transparent;
    background-image: linear-gradient(270deg,#6254e7 0%,#9289f1 100%);
    color: #fff;
}
.button-group a:nth-child(1):hover{
    background-image: linear-gradient(270deg,#6254e7 0%,#9289f1 40%);
}
.button-group a:nth-child(2)
{
    color: #F39D00;
    font-weight: bold;
}
.intropart1 .right{
    width: 50%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.intropart1 .right img{
    width: 100%;
}