.comment-box h1{
    font-size: 1.5rem;
}
.comment-box .comment{
    width: 400px;
    height:100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    box-shadow: 0 1px 4px rgba(0,0,0,.2);
    margin: 1rem 0.5rem;
    padding: 1rem;
    border-radius: 4px;
}

.comment-box .comment .top{
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
.comment-box .comment .top .left{
    width: 50px;
    height: 100%;
}
.comment-box .comment .top .left img{
    width: 100%;
    height: 100%;
    padding: 0.3rem;
    border-radius: 50%;
    border: 2px solid #3330a3;
    object-fit: fill;
}
.comment-box .comment .top .right{
    width:300px;
    height: 100%;
    margin: 0 1rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}
.comment-box .comment .top .right h2{
    font-size: 1.2rem;
    margin: 0;
}

.comment-box .comment .top .right h3{
    font-size: 0.8rem;
    margin: 0;
}

.comment-box .comment .bottom h4{
    font-size: 1.2rem;
}