.contactform{
    margin-top: 5rem;
    width: 90%;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contactform .left{
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.contactform .left img{
    width: 80%;
}

.contactform .right{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content:center;
    margin-left: 5rem;
}
.contactform .right h1{
    font-size: 1.2rem;
    color: #005194;
}
.contactform .right h2{
    font-size: 2.5rem;
    color: #ffa04d;
}
.contactform .right h3{
    font-size: 1.2rem;
    color: #a0a0a0;
}
.contactform .right .contact-form{
    margin: 5rem 0;
    display: flex;
    flex-direction: column;
    width: 80%;
    justify-content: center;
    align-items: flex-start;
}

.contactform .right .contact-form .form-group{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 10vh;
    margin: 0.2rem 0;
}

.contactform .right .contact-form button{
    border: none;
    background-image: linear-gradient(96deg,#6254e7 0%,#9289f1 100%);
    padding: 12px 20px;
    color: #fff;
    border-radius: 4px;
    font-size: 0.9rem;
    margin-top: 2rem;
    cursor: pointer;
}

.contactform .right .contact-form button:hover{
    background-image: linear-gradient(96deg,#6254e7 0%,#9289f1 20%);
}