.blogExpand{
    width: 100%;
    height: 100%;
    margin-top: 15vh;
    display: flex;
    padding: 2rem 0;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
}
.blog-detail{
    width: 80%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.blog-detail h1{
    font-size: 3rem;
    margin: 0.5rem 0;
    font-weight: lighter;
}

.blog-detail h2{
    font-size: 1.5rem;
    margin: 0.5rem 0;
    color: #aeaeae;
    font-weight: lighter;
}

.blog-detail .utils{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.blog-detail .utils h3{
    font-size: 1.2rem;
    margin: 0.5rem 0;
    font-weight: lighter;
}
.blog-detail .utils h3 i:nth-child(1)
{
    color: #3330a3;
}
.blog-detail .utils h3 i:nth-child(2)
{
    color: #c5c5c5;
}
.blog-detail .utils .social-share{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.blog-detail .utils .social-share a{
    font-size: 1.8rem;
    margin: 0 0.8rem;
}

.blog-detail .utils .social-share a:hover{
    transform: translateY(-10px);
    transition: transform .5s cubic-bezier(.29,1.42,.79,1) 0s,-webkit-transform .5s cubic-bezier(.29,1.42,.79,1) 0s;
}
.blog-detail img{
    width: 100%;
    height: 50vh;
    object-fit: cover;
    border-radius: 4px;
    margin: 1rem 0;
    object-fit: contain;
}

.blog-detail .blog-body{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.blog-detail .blog-body h1{
    font-size: 2rem;
    font-weight: bold;
}
.blog-detail .blog-body h2{
    font-size: 1.5rem;
    font-weight: bold;
}
.blog-detail .blog-body p{
    font-size: 1.2rem;
}
.blog-detail .blog-body .ql-syntax{
    background-color: darkgray;
    padding: 0.5rem;
}

.blog-detail .create-comment{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 2rem 0;
}
.blog-detail .create-comment form{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items:flex-start;
}

.blog-detail .create-comment form .input-field{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items:flex-start;
}
.blog-detail .create-comment form .input-field textarea{
    width: 30vw;
    height: 20vh;
    padding: 1rem;
    font-size: 1.2rem;
    margin: 1rem 0;
    outline: none;
}

.blog-detail .create-comment form button{
    width: 120px;
    height: 50px;
    border: 1.5px solid #3330a3;
    background-color: transparent;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1.2rem;
    color: #3330a3;
}

.blog-detail h4{
    font-size: 1.5rem;
    margin:2rem 0
}

.blog-detail h4 span{
    color: #3330a3;
}