.seoanalysis{
    margin-top: 5rem;
    width: 90%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.seoanalysis .left{
    width: 50%;
    height: 100%;
}

.seoanalysis .left img{
    width: 100%;
}

.seoanalysis .right{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content:center;
    margin-left: 5rem;
}
.seoanalysis .right h1{
    font-size: 1.2rem;
    color: #005194;
}
.seoanalysis .right h2{
    font-size: 2.5rem;
    color: #ffa04d;
}
.seoanalysis .right h3{
    font-size: 1.2rem;
    color: #a0a0a0;
}
.seoanalysis .right .seo-analysis-form{
    margin: 5rem 0;
    display: flex;
    flex-direction: column;
    width: 80%;
    justify-content: center;
    align-items: flex-start;
}

.seoanalysis .right .seo-analysis-form .form-group{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 10vh;
    margin: 0.2rem 0;
}
.seoanalysis .right .seo-analysis-form button{
    border: none;
    padding: 0;
    cursor: pointer;
}
.seoanalysis .right .seo-analysis-form button div{
    width: 150px;
    height: 50px;
    border-radius: 4px;
    background-image: linear-gradient(96deg,#6254e7 0%,#9289f1 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
}

.seoanalysis .right .seo-analysis-form button div h6{
    font-size: 1rem;
    color: #fff;
    transform: translateX(-0px);
    transition: transform .5s cubic-bezier(.29,1.42,.79,1) 0s,-webkit-transform .5s cubic-bezier(.29,1.42,.79,1) 0s;
}

.seoanalysis .right .seo-analysis-form button div i{
    color: #fff;
    margin: 0 0.8rem;
    position: absolute;
    right: -50px;
    transition: position .5s cubic-bezier(.29,1.42,.79,1) 0s,-webkit-transform .5s cubic-bezier(.29,1.42,.79,1) 0s;
}

.seoanalysis .right .seo-analysis-form button:hover div h6{
    transform: translateX(-10px);
    font-size: 0.9rem;
    transition: transform .5s cubic-bezier(.29,1.42,.79,1) 0s,-webkit-transform .5s cubic-bezier(.29,1.42,.79,1) 0s;
}

.seoanalysis .right .seo-analysis-form button:hover div i{
    transform: translateX(-50px);
    transition: transform .5s cubic-bezier(.29,1.42,.79,1) 0s,-webkit-transform .5s cubic-bezier(.29,1.42,.79,1) 0s;
}