.latestblog{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}
.latestblog  h1{
    font-size: 1.2rem;
    color: #3330a3;
}
.latestblog .latestblog-list{
    width: 100%;
    height: 100%;
}
.latestblog .latestblog-card{
    width: 600px;
    height:200px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding:1rem;
    cursor: pointer;
    -webkit-box-shadow: 0px 0px 3px 5px rgba(0,0,0,0.1);
    -moz-box-shadow: 0px 0px 3px 5px rgba(0,0,0,0.1);
    box-shadow: 0px 0px 3px 5px rgba(0,0,0,0.1); 
    border-radius: 4px;
    margin: 1rem 0;
}

.latestblog  .latestblog-card h1{
    color: #000;
    font-size: 1.2rem;
    margin: 0.3rem 0;
}
.latestblog  .latestblog-card h2{
    color: #000;
    font-size: 0.8rem;
    margin: 0.2rem 0;
    color: #a3a3a3;
}
.latestblog  .latestblog-card h3{
    color: #000;
    font-size: 0.8rem;
    margin: 0.2rem 0;
    color: #f4a411;
}
.latestblog  .latestblog-card h4{
    color: #000;
    font-size: 1rem;
    margin: 0.2rem 0;
}
.latestblog  .latestblog-card h4 i{
    color: #000;
    font-size: 1rem;
    margin: 0.2rem 0.5rem;
    color: #3330a3;
}
.latestblog  .latestblog-card h5{
    color: #000;
    font-size: 0.8rem;
    margin: 0.2rem 0;
    color: #c5c5c5;
}
.latestblog  .right{
    width: 40%;
    height: 100%;
}